.gallery-page {
  background-color: #f2d7d3;
  background: linear-gradient(45deg, #e7b0c6 0%, #f9e2ee 100%);
  height: 100vh;
  width: 100%;
}
.loader {
  background-color: #f2d7d3;
  background: linear-gradient(45deg, #e7b0c6 0%, #f9e2ee 100%);

  position: fixed;
  display: block;
  width: 100%;
  width: 100vw;
  height: 100%;
  height: 100vh;
  z-index: 999;
  overflow: hidden;
  transition: all 0.35s ease-in-out;
}
.loader .lds-ripple {
  position: absolute;
  display: block;
  width: 64px;
  height: 64px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.loader .lds-ripple div {
  position: absolute;
  border: 4px solid #ffffff;
  opacity: 1;
  border-radius: 50%;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.loader .lds-ripple div:nth-child(2) {
  animation-delay: -0.5s;
}
.loader.is-loaded {
  opacity: 0;
  visibility: hidden;
  z-index: -99;
}

@keyframes lds-ripple {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
  }
}
header {
  font-family: "Poppins", sans-serif;
  position: fixed;
  display: block;
  width: 100%;
  height: 48px;
  top: 0;
  right: 0;
  left: 0;
  z-index: 100;
}
header .header-inner {
  padding: 32px 1.5%;
}
@media screen and (max-width: 768px) {
  header .header-inner {
    padding: 16px 16px 0;
  }
}
@media screen and (max-width: 640px) {
  header .header-inner {
    padding: 16px 16px 0 16px;
  }
}
header .header-inner .logo {
  position: relative;
  display: inline;
  width: 36px;
  height: 36px;
  float: left;
}
header .header-inner .logo a {
  position: relative;
  display: block;
  width: 36px;
  height: 36px;

  border-radius: 4px;
  transform: rotate(15deg);
  transition: all 0.35s ease-in-out;
}
header .header-inner .logo a:hover {
  box-shadow: 0 15px 31px rgba(0, 0, 0, 0.12), 0 5px 31px rgba(0, 0, 0, 0.06);
}
header .header-inner nav {
  position: relative;
  display: inline;
  margin: 0;
  padding: 0 16px 0 24px;
  list-type-style: none;
}
header .header-inner nav li {
  position: relative;
  display: inline;
  margin-right: 16px;
}
header .header-inner nav li a {
  font-family: "Poppins", sans-serif;
  position: relative;
  font-size: 15px;
  color: #ffffff;
  text-decoration: none;
  line-height: 40px;
  transition: all 0.35s ease-in-out;
}
@media screen and (max-width: 768px) {
  header .header-inner nav li a {
    font-size: 13px;
    clear: both;
  }
}
@media screen and (max-width: 360px) {
  header .header-inner nav li a {
    font-size: 11px;
  }
}
header .header-inner nav li a span {
  position: relative;
  z-index: 2;
}
header .header-inner nav li a:hover {
  color: rgba(36, 36, 36, 0.45);
}

section.content-holder {
  position: fixed;
  display: block;
  width: 100%;
  max-width: 640px;
  height: 100%;
  overflow: visible;
  left: 0;
  top: 0;
  bottom: 0;
}
section.content-holder .content {
  position: relative;
  display: block;
  width: 100%;
  max-width: 640px;
  height: auto;
  overflow: visible;
  left: 3%;
  top: 50vh;
  transform: translateY(-50%);
}
@media (max-width: 768px) {
  section.content-holder .content {
    position: absolute;
    top: auto;
    transform: none;
    left: 16px;
    bottom: 16px;
  }
}
section.content-holder .content h1 {
  color: #ffffff;
  font-family: "Poppins", sans-serif;
  font-size: 96px;
  line-height: 102px;
  font-weight: 900;
}
@media (max-width: 768px) {
  section.content-holder .content h1 {
    font-size: 48px;
    line-height: 56px;
  }
}
section.content-holder .content h1 span {
  font-size: 62px;
}
section.content-holder .content h1 span svg {
  height: 92px;
}
@media (max-width: 768px) {
  section.content-holder .content h1 span svg {
    height: 54px;
  }
}
section.content-holder .content p {
  color: #ffffff;
  opacity: 0.5;
  width: calc(100% - 48px);
  max-width: 340px;
  padding: 16px 0;
}
section.content-holder .content form .label {
  position: relative;
  display: block;
  width: 100%;
  width: calc(100% - 56px);
  padding-right: 40px;
  max-width: 290px;
  overflow: visible;
}
@media (max-width: 768px) {
  section.content-holder .content form .label {
    max-width: 48px;
  }
}
section.content-holder .content form .label .input {
  font-family: "Poppins", sans-serif;
  background-color: rgba(255, 255, 255, 0.25);
  font-size: 13px;
  color: #ffffff;
  line-height: 48px;
  border-radius: 4px;
  border: none;
  margin-top: 16px;
  padding: 0px 16px;
  width: 100%;
  outline: 0px none;
  outline: 0px;
  letter-spacing: 1px;
  transition: all 0.35s ease-in-out;
}
@media (max-width: 768px) {
  section.content-holder .content form .label .input {
    max-width: 0;
    padding: 0;
    height: 0;
  }
}
section.content-holder .content form .label .input::placeholder {
  color: #ffffff;
  opacity: 1;
}
section.content-holder .content form .label .input:-ms-input-placeholder {
  color: #ffffff;
}
section.content-holder .content form .label .input::-ms-input-placeholder {
  color: #ffffff;
}
section.content-holder .content form .label .input:focus {
  box-shadow: 0 15px 31px rgba(0, 0, 0, 0.12), 0 5px 31px rgba(0, 0, 0, 0.06);
}
section.content-holder .content form .label .input:hover {
  box-shadow: 0 15px 31px rgba(0, 0, 0, 0.12), 0 5px 31px rgba(0, 0, 0, 0.06);
}
section.content-holder .content form .label .submit {
  position: absolute;
  display: block;
  width: 44px;
  height: 66px;
  line-height: 80px;
  text-align: center;
  background-color: transparent;
  border: none;
  top: 0;
  right: 0;
  color: #ffffff;
  outline: 0px none;
  outline: 0px;
}
@media (max-width: 768px) {
  section.content-holder .content form .label .submit {
    position: relative;
    background-color: #ffffff;
    width: 48px;
    height: 48px;
    line-height: 50px;
    border-radius: 4px;
    background-color: #f2d7d3;
    background: linear-gradient(45deg, #e7b0c6 0%, #f9e2ee 100%);

    margin-top: -16px;
  }
}
section.grid-holder {
  position: relative;
  display: block;
  width: 100%;
  height: auto;
  overflow: visible;
}
section.grid-holder .masonry {
  width: 100%;
  max-width: 1024px;
  float: right;
  display: flex;
  flex-wrap: wrap;
  height: 100vh;
  overflow: scroll;
}
@media (max-width: 1360px) {
  section.grid-holder .masonry {
    max-width: calc(100% - 320px);
  }
}
@media (max-width: 768px) {
  section.grid-holder .masonry {
    max-width: 480px;
  }
}
@media (max-width: 600px) {
  section.grid-holder .masonry {
    max-width: 320px;
  }
}
@media (max-width: 480px) {
  section.grid-holder .masonry {
    max-width: 80%;
  }
}
section.grid-holder .masonry .close-detail-view {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 0;
  opacity: 0;
  visibility: 0;
}
section.grid-holder .masonry .close-detail-view.active {
  z-index: 10;
}
section.grid-holder .masonry .masonry__item {
  background-color: #242424;
  margin: 1%;
  width: 23%;
  border-radius: 8px;
  overflow: hidden;
  opacity: 0.15;
  cursor: pointer;
  transition: box-shadow 0.35s ease-in-out, opacity 0.35s ease-in-out;
  z-index: 1;
  will-change: transform;
}
@media (max-width: 1360px) {
  section.grid-holder .masonry .masonry__item {
    width: 31.33333%;
  }
}
@media (max-width: 1024px) {
  section.grid-holder .masonry .masonry__item {
    width: 48%;
  }
}
section.grid-holder .masonry .masonry__item figure {
  position: absolute;
  display: block;
  width: 100%;
  width: calc(100% - 32px);
  height: 100%;
  height: calc(100% - 32px);
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: 0;
  padding: 16px;
}
section.grid-holder .masonry .masonry__item figure figcaption {
  font-family: "Poppins", sans-serif;
  position: relative;
  z-index: 2;
  color: #ffffff;
  font-weight: 300;
  font-size: 13px;
  line-height: 13px;
  margin: 0;
  padding: 0;
  padding-bottom: 8px;
}
section.grid-holder .masonry .masonry__item figure .background {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-background-size: cover !important;
  -moz-background-size: cover !important;
  -o-background-size: cover !important;
  background-size: cover !important;
  /* opacity: 0;
  visibility: hidden; */
  z-index: 1;
  transform: scale(1.5);
}
section.grid-holder .masonry .masonry__item figure .content h2 {
  font-family: "Poppins", sans-serif;
  color: #ffffff;
  font-size: 32px;
  line-height: 38px;
  font-weight: 900;
  word-spacing: 9999999px;
  opacity: 0.15;
  visibility: visible;
  z-index: 1;
  backface-visibility: hidden;
  transform: translateZ(0);
